body{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


.header{
  width: 100%;
  height: 60px;
  display: flex;
  flex-direction: row;
  background-color: #000;
  justify-content: space-between;
}

.logo img{
  width: 200px;
}

.nav{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.nav p {
  margin: 10px;
  color: #fff;
}